.services {
  background-color: $black;
  color: $white-base;
  padding: 65px 0 140px;

  @include vp-1023 {
    padding: 55px 0 75px;
  }

  @include vp-767 {
    padding: 47px 0 86px;
  }

  &__title {
    @include rc-r-s36-h51;
    margin: 0 0 54px;

    & > span:last-child {
      display: none;
    }

    @include vp-1023 {
      margin-bottom: 38px;
    }

    @include vp-767 {
      @include rc-r-s28-h39;
      margin-bottom: 29px;

      & > span:first-child {
        display: none;
      }

      & > span:last-child {
        display: block;
      }
    }
  }

  &__list {
    display: grid;
    grid-template-columns: 70fr 17fr 70fr 17fr 70fr;
    grid-auto-rows: 1fr;
    min-height: 650px;

    @include vp-1023 {
      grid-template-columns: 1fr;
      grid-auto-rows: minmax(250px, 1fr);
      gap: 30px;
      min-height: none;
    }
  }

  &__card {
    background-color: $dark-blue;
    overflow: hidden;

    @include vp-1024 {
      &:nth-child(3n-2) {
        grid-column: 1/2;

        img {
          top: -202px;
          left: -188px;
        }
      }

      &:nth-child(3n-1) {
        grid-column: 3/4;

        img {
          top: -207px;
          left: -534px;
        }
      }

      &:nth-child(3n) {
        grid-column: 5/-1;

        img {
          top: -196px;
          left: -894px;
        }
      }
    }

    @include vp-1023 {
      &:nth-child(3n-2) img {
        top: -634px;
        left: -244px;
      }

      &:nth-child(3n-1) img {
        top: -399px;
        left: -628px;
      }

      &:nth-child(3n) img {
        top: -444px;
        left: -148px;
      }
    }

    @include vp-767 {
      &:nth-child(3n-2) img {
        top: -384px;
        left: -375px;
      }

      &:nth-child(3n-1) img {
        top: -622px;
        left: -365px;
      }

      &:nth-child(3n) img {
        top: -479px;
        left: -884px;
      }
    }
  }

  &__link {
    display: block;
    position: relative;
    min-width: 100%;
    min-height: 100%;
    padding: 47px 40px;

    &::before {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: $black-mask;
      content: "";
      z-index: 1;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 1493px;
      max-width: none;
    }

    @include vp-1023 {
      padding: 37px 45px;
    }

    @include vp-767 {
      padding: 27px 30px;
    }
  }

  &__text {
    @include rc-r-s20-h30;
    position: relative;
    width: 190px;
    z-index: 2;
    margin: 0;

    @include vp-1023 {
      width: 150px;
    }

    @include vp-767 {
      @include rc-r-s18-h30;
      width: 130px;
    }
  }

  &__icon {
    position: absolute;
    display: block;
    background-color: $azure;
    width: 60px;
    height: 60px;
    padding: 25px;
    padding-left: 30px;
    border-radius: 3px 0 0;
    right: 0;
    bottom: 0;
    z-index: 2;

    @include vp-767 {
      width: 40px;
      height: 40px;
      padding: 17px;
      padding-left: 20px;
    }
  }
}
