.advant {
  margin: 120px 0;

  @include vp-1023 {
    margin: 80px 0 75px;
  }

  @include vp-767 {
    margin: 50px 0 47px;
  }
}

.advant__container {
  display: grid;
  grid-template-rows: 1fr 1fr;
  gap: 30px;

  @include vp-767 {
    grid-template-rows: none;
  }
}

.advant__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 30px;

  @include vp-767 {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;

    &:first-child .advant__card {
      min-height: 340px;
    }

    &:last-child .advant__card {
      min-height: 325px;

      .advant__title {
        margin: 19px 0 13px;
      }
    }
  }
}

.advant__card {
  position: relative;
  min-height: 300px;
  padding: 39px 45px 35px;
  border: 1px solid $light-grey;
  border-left: none;
  z-index: 1;
  transition: background-color $trans-default, color $trans-default, border $trans-default;

  @include vp-1023 {
    min-height: 360px;
    padding-bottom: 15px;
  }

  @include vp-1023 {
    min-height: 340px;
    padding: 29px 30px 30px;
  }

  &::before {
    position: absolute;
    top: -1px;
    bottom: -1px;
    left: 0;
    background-color: $azure;
    width: 5px;
    content: "";
  }

  .advant__icon {
    display: block;
    fill: $azure;
    transition: fill $trans-default;

    @include vp-767 {
      width: 30px;
      height: 30px;
    }
  }

  .advant__title {
    @include rc-r-s24-h34;
    margin: 21px 0 13px;
    color: $azure;
    transition: color $trans-default;

    @include vp-1023 {
      margin: 16px 0 6px;
    }

    @include vp-767 {
      margin-bottom: 9px;

      @include rc-r-s20-h28;
    }
  }

  .advant__text {
    @include pt-r-s16-h22;
    margin: 0;

    @include vp-767 {
      min-height: 140px;

      @include pt-r-s14-h19;
    }
  }

  @include hover {
    background-color: $azure;
    border-color: $azure;
    color: $white-sec;

    &::before {
      z-index: -1;
      width: 100%;
      transition: width $trans-default;
    }

    .advant__icon {
      fill: $white-sec;
    }

    .advant__title {
      color: $white-sec;
    }
  }
}
